import api from './api'

const login = data => api({
  method: 'post',
  url: '/api/login',
  data
})

const getIam = () => api({
  method: 'get',
  headers: {
    Authorization: `Bearer ${window.localStorage.getItem('token')}`
  },
  url: '/api/iam'
})

export {
  login,
  getIam
}
