import API from '@/api'
import router from '@/router'

export default {
  namespaced: true,
  state: {
    authForm: {},
    user: {}
  },
  getters: {
    authForm: state => state.authForm,
    user: state => state.user
  },
  mutations: {
    changeAuthForm (state, { key, value }) {
      state.authForm[key] = value
    },
    changeUserData (state, data) {
      state.user = data
    }
  },
  actions: {
    async login ({ state, commit, dispatch }) {
      API.admin.login({ ...state.authForm })
        .then(({ data }) => {
          window.localStorage.setItem('token', data.token)
          dispatch('getUser')
        })
        .catch(() => {
          router.push({ name: 'auth' })
        })
    },
    getUser ({ commit }) {
      return API.admin.getIam()
        .then(({ data }) => {
          commit('changeUserData', data.user)
          router.push({ name: 'admin' })
        })
        .catch(() => {
          router.push({ name: 'auth' })
        })
    }
  }
}
