import API from '@/api'
import router from '@/router'

export default {
  getLessons ({ commit }) {
    API.lessons.getLessons()
      .then(({ data }) => {
        commit('setLessons', data)
      })
  },
  goToLesson (context, id) {
    API.lessons.getLessonUrl(id)
      .then(({ data }) => {
        router.push({
          name: 'lessonItem',
          params: {
            hash: data
          }
        })
      })
  },
  createLesson ({ state, commit }) {
    commit('changeEditInProgress', true)

    const formData = new FormData()

    for (const key in state.editForm) {
      if (Object.prototype.hasOwnProperty.call(state.editForm, key)) {
        formData.set(key, state.editForm[key])
      }
    }

    return API.lessons.createLesson(formData)
      .then(({ data }) => {
        const newData = [...state.lessons, data]

        commit('setLessons', newData)
      })
      .catch(() => {
        alert('При создании урока произошла ошибка')
      })
      .finally(() => {
        commit('changeEditInProgress', false)
      })
  },
  deleteLesson ({ state, commit }, id) {
    API.lessons.deleteLesson(id)
      .then(() => {
        const index = state.lessons.findIndex(item => item.id === id)

        commit('removeLessonFromArray', index)
      })
  },
  updateLesson ({ state, commit }) {
    commit('changeEditInProgress', true)

    const { id } = state.editForm
    const formData = new FormData()

    for (const key in state.editForm) {
      if (Object.prototype.hasOwnProperty.call(state.editForm, key)) {
        formData.set(key, state.editForm[key])
      }
    }

    formData.set('_method', 'patch')

    return API.lessons.updateLesson({
      id,
      data: formData
    })
      .then(({ data }) => {
        const index = state.lessons.findIndex(item => item.id === id)

        commit('updateLessonItem', { index, data })
      })
      .catch(() => {
        alert('При создании урока произошла ошибка')
      })
      .finally(() => {
        commit('changeEditInProgress', false)
      })
  }
}
