<template>
  <div class="auth-page flex-wrap justify-center align-center h100">
    <form class="flex-column auth-form">
      <label
        v-for="(input, index) in authInputs"
        :key="index"
        class="w100 default-label"
      >
        <p>
          {{ input.label }}
        </p>
        <input
          :type="input.type"
          :placeholder="input.placeholder"
          class="w100"
          @input="changeAuthForm({
            key: input.key,
            value: $event.target.value
          })"
        >
      </label>

      <input
        class="default-button"
        type="submit"
        value="Войти"
        @click.prevent="login"
        @keydown.enter="login"
      >
    </form>
  </div>
</template>

<script>
import {
  mapMutations,
  mapActions
} from 'vuex'

export default {
  data: () => ({
    authInputs: [
      {
        type: 'text',
        placeholder: 'Введите логин',
        label: 'Логин',
        key: 'login'
      },
      {
        type: 'password',
        placeholder: 'Введите пароль',
        label: 'Пароль',
        key: 'password'
      }
    ]
  }),
  methods: {
    ...mapMutations('Auth', {
      changeAuthForm: 'changeAuthForm'
    }),
    ...mapActions('Auth', {
      login: 'login'
    })
  }
}
</script>
