import API from '@/api'
import router from '@/router'

export default {
  namespaced: true,
  state: {
    lesson: {}
  },
  getters: {
    lesson: state => state.lesson
  },
  mutations: {
    setLesson (state, data) {
      state.lesson = data
    }
  },
  actions: {
    getLesson ({ commit }, hash) {
      API.lessons.getLessonItem(hash)
        .then(({ data }) => {
          commit('setLesson', data)
        })
        .catch(() => {
          router.push('/404')
        })
    }
  }
}
