<template>
  <div class="lessons flex-wrap">
    <div class="w100 flex justify-space-between">
      <p>Список уроков</p>

      <button
        class="default-button mt-0"
        @click="addNewLesson"
      >
        Добавить урок
      </button>
    </div>

    <div class="lessons__items w100">
      <lesson-item
        v-for="(item, index) in lessons"
        :key="index"
        :lesson-data="item"
        @openModal="editModalIsActive = true"
      />
    </div>

    <edit-modal
      v-if="editModalIsActive"
      :is-active="editModalIsActive"
      @closeModal="editModalIsActive = false"
    />
  </div>
</template>

<script>
import {
  mapGetters,
  mapMutations,
  mapActions
} from 'vuex'
import { LessonItem } from './ui'
import { EditModal } from './views'
import { initLessons } from '@/inits'

export default {
  components: {
    LessonItem,
    EditModal
  },
  data: () => ({
    editModalIsActive: false
  }),
  created () {
    this.getLessons()
  },
  computed: {
    ...mapGetters('Lessons', {
      lessons: 'lessons'
    })
  },
  methods: {
    ...mapMutations('Lessons', {
      setEditForm: 'setEditForm'
    }),
    ...mapActions('Lessons', {
      getLessons: 'getLessons'
    }),
    addNewLesson () {
      this.setEditForm(initLessons.getLesson())
      this.editModalIsActive = true
    }
  }
}
</script>
