import api from './api'

const apiWithToken = config => api({
  ...config,
  headers: {
    ...config.headers,
    Authorization: `Bearer ${window.localStorage.getItem('token')}`
  }
})

const getLessons = () => apiWithToken({
  method: 'get',
  url: '/api/lessons'
})

const getLessonUrl = id => apiWithToken({
  method: 'post',
  url: `/api/lessons/hash/${id}`
})

const createLesson = data => apiWithToken({
  method: 'post',
  headers: {
    'Content-Type': 'multipart/form-data'
  },
  url: '/api/lessons',
  data
})

const deleteLesson = id => apiWithToken({
  method: 'delete',
  url: `/api/lessons/${id}`
})

const updateLesson = ({ id, data }) => apiWithToken({
  method: 'post',
  headers: {
    'Content-Type': 'multipart/form-data'
  },
  url: `/api/lessons/${id}`,
  data
})

const getLessonItem = hash => apiWithToken({
  method: 'get',
  url: `/api/lessons/hash/${hash}`
})

export {
  getLessons,
  getLessonUrl,
  createLesson,
  deleteLesson,
  updateLesson,
  getLessonItem
}
