import axios from 'axios'
import qs from 'qs'

const ApiHost = 'https://moroshka-english.xyz/'

const configure = {
  baseURL: ApiHost,
  headers: {
    Authorization: `Bearer ${window.localStorage.getItem('token')}`
  },
  paramsSerializer: params => qs.stringify(params)
}

const api = axios.create(configure)

export default api
