<template>
  <div class="lesson-item flex w100 justify-space-between align-center">
    <p class="lesson-item__text">
      <b>{{ lessonData.name }}</b>
      (создан {{ formattedDate }})
    </p>

    <div class="lesson-item__buttons">
      <button
        class="lesson-item__url-button"
        @click="goToLesson(lessonData.id)"
      >
        Перейти к уроку
      </button>

      <button
        class="lesson-item__edit-button"
        @click.prevent="openEditModal"
      >
        Редактировать
      </button>

      <button
        class="lesson-item__url-button"
        @click="deleteHandler(lessonData)"
      >
        Удалить
      </button>
    </div>
  </div>
</template>

<script>
import {
  mapMutations,
  mapActions
} from 'vuex'
import moment from 'moment'

export default {
  props: {
    lessonData: {
      type: Object,
      default: () => ({}),
      required: true
    }
  },
  computed: {
    formattedDate () {
      return moment(this.lessonData.createdAt).format('DD.MM.YYYY')
    }
  },
  methods: {
    ...mapMutations('Lessons', {
      setEditForm: 'setEditForm'
    }),
    ...mapActions('Lessons', {
      deleteLesson: 'deleteLesson',
      goToLesson: 'goToLesson'
    }),
    openEditModal () {
      this.setEditForm(this.lessonData)
      this.$emit('openModal')
    },
    deleteHandler (data) {
      const result = confirm(`Действительно удалить урок "${data.name}"?`)

      if (result) {
        this.deleteLesson(data.id)
      }
    }
  }
}
</script>
