export default {
  setLessons (state, data) {
    state.lessons = data
  },
  removeLessonFromArray (state, index) {
    state.lessons.splice(index, 1)
  },
  updateLessonItem (state, { index, data }) {
    state.lessons[index] = data
  },
  changeEditForm (state, { key, value }) {
    state.editForm[key] = value
  },
  setEditForm (state, data) {
    state.editForm = { ...data }
  },
  changeEditInProgress (state, value) {
    state.editInProgress = value
  }
}
