<template>
  <div class="lesson-page flex-wrap">
    <div class="w100 lesson-page__buttons flex justify-center">
      <button @click="resizeVideo(50)">
        +
      </button>
      <button @click="resizeVideo(-50)">
        -
      </button>
    </div>

    <div class="lesson-page__content w100 flex align-start">
      <div
        class="lesson-page__left-part"
      >
        <p class="lesson-name">
          {{ lesson.name }}
        </p>

        <div
          ref="videoPlayerWrapper"
          class="lesson-video-wrapper"
        >
          <video-player
            v-if="lesson.id"
            class="video-player-box"
            ref="videoPlayer"
            :options="playerOptions"
            :playsinline="true"
            customEventName="customstatechangedeventname"
          />
        </div>
      </div>

      <div class="lesson-page__right-part" v-html="lesson.text">

      </div>
    </div>
  </div>
</template>

<script>
import {
  mapGetters,
  mapActions
} from 'vuex'
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'

export default {
  components: {
    videoPlayer
  },
  created () {
    this.getLesson(this.$route.params.hash)
  },
  computed: {
    ...mapGetters('LessonView', {
      lesson: 'lesson'
    }),
    playerOptions () {
      return {
        muted: true,
        autoplay: true,
        language: 'en',
        responsive: true,
        playbackRates: [0.5, 1.0, 1.5, 2.0],
        fluid: true,
        aspectRatio: '4:4',
        breakpoints: {
          tiny: 300,
          xsmall: 400,
          small: 500,
          medium: 600,
          large: 700,
          xlarge: 800,
          huge: 900
        },
        sources: [{
          type: 'video/mp4',
          src: this.lesson.video_url
        }]
      }
    }
  },
  methods: {
    ...mapActions('LessonView', {
      getLesson: 'getLesson'
    }),
    resizeVideo (value) {
      const videoWrapper = this.$refs.videoPlayerWrapper

      videoWrapper.style.width = `${videoWrapper.offsetWidth - 40 + value}px`
    }
  }
}
</script>
